<template>
  <section v-bind:style="`background-color: ${selectedColor}`" id="main_container">
    <quote-machine @changeColor="changeC($event)"></quote-machine>
  </section>
</template>

<script>
import QuoteMachine from "../src/components/QuoteMachine.vue";

export default {
  name: "App",
  components: {
    QuoteMachine,
  },
  data() {
    return {
      selectedColor: null,
    };
  },

  methods: {
    changeC(color) {
      this.selectedColor = color;
    },
  },
};
</script>

