<template>
  <section>
    <div id="quote-box">
      <h1 v-bind:style="`color:${selectedColor}; transition: color 2s`" id="text">
        <svg
          aria-hidden="true"
          focusable="false"
          data-prefix="fas"
          data-icon="quote-left"
          class="svg-inline--fa fa-quote-left fa-w-16"
          role="img"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 512 512"
        >
          <path
            fill="currentColor"
            d="M464 256h-80v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8c-88.4 0-160 71.6-160 160v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48zm-288 0H96v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8C71.6 32 0 103.6 0 192v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48z"
          ></path>
        </svg>
        {{ randomQuote }}
      </h1>

      <div class="author_container">
        <span v-bind:style="`color:${selectedColor}; transition: color 2s`" id="author"
          >- {{ author }}</span
        >
      </div>
      <div class="button_container">
        <a
          target="_blank"
          id="tweet-quote"
          v-bind:href="`https://twitter.com/intent/tweet?text=&quot;${randomQuote}&quot; -${author}`"
          ><svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fab"
            data-icon="twitter-square"
            class="svg-inline--fa fa-twitter-square fa-w-14"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 448 512"
          >
            <path
              style="transition: fill 2s"
              v-bind:fill="selectedColor"
              d="M400 32H48C21.5 32 0 53.5 0 80v352c0 26.5 21.5 48 48 48h352c26.5 0 48-21.5 48-48V80c0-26.5-21.5-48-48-48zm-48.9 158.8c.2 2.8.2 5.7.2 8.5 0 86.7-66 186.6-186.6 186.6-37.2 0-71.7-10.8-100.7-29.4 5.3.6 10.4.8 15.8.8 30.7 0 58.9-10.4 81.4-28-28.8-.6-53-19.5-61.3-45.5 10.1 1.5 19.2 1.5 29.6-1.2-30-6.1-52.5-32.5-52.5-64.4v-.8c8.7 4.9 18.9 7.9 29.6 8.3a65.447 65.447 0 0 1-29.2-54.6c0-12.2 3.2-23.4 8.9-33.1 32.3 39.8 80.8 65.8 135.2 68.6-9.3-44.5 24-80.6 64-80.6 18.9 0 35.9 7.9 47.9 20.7 14.8-2.8 29-8.3 41.6-15.8-4.9 15.2-15.2 28-28.8 36.1 13.2-1.4 26-5.1 37.8-10.2-8.9 13.1-20.1 24.7-32.9 34z"
            ></path></svg
        ></a>
        <button
          v-bind:style="`background-color: ${selectedColor}; transition: background-color 2s`"
          v-on:click="callQuotes"
          id="new-quote"
        >
          New Quote
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      randomQuote: null,
      author: null,
      colors: [
        "#16a085",
        "#27ae60",
        "#2c3e50",
        "#f39c12",
        "#e74c3c",
        "#9b59b6",
        "#FB6964",
        "#342224",
        "#472E32",
        "#BDBB99",
        "#77B1A9",
        "#73A857",
      ],
      selectedColor: null,
    };
  },

  created() {
    this.callQuotes();
  },

  methods: {
    callQuotes() {
      fetch(
        "https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json"
      )
        .then((response) => response.json())
        .then((data) => {
          let randomNumber = Math.floor(Math.random() * (102 - 0) + 0);
          let randomColor = Math.floor(Math.random() * (12 - 0) + 0);
          this.randomQuote = data.quotes[randomNumber].quote;
          this.author = data.quotes[randomNumber].author;
          this.selectedColor = this.colors[randomColor];
          this.$emit("changeColor", this.selectedColor);
          console.log(this.selectedColor);
        });
    },
  },
};
</script>

<style scoped>

.fa-twitter-square {
  width: 40px;
  fill: salmon;
}

.fa-quote-left {
  width: 24px;
  margin-right: 4px;
  position: relative;
  top: 2px;
}
button {
  background-color: salmon;
  color: white;
  border: none;
  padding: 0px 15px;
}

button:hover,
a:hover {
  cursor: pointer;
  filter: brightness(110%);
}
.author_container,
.button_container {
  display: flex;
}

.author_container {
  justify-content: flex-end;
}
.button_container {
  justify-content: space-between;
  margin-top: 20px;
}
#quote-box {
  width: 450px;
  padding: 35px;
  background-color: white;
}

#quote-box,
button {
  border-radius: 5px;
}

#text {
  text-align: center;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/*Dynamic SVG Color Classes*/
.color_16a085 {
  fill: #16a085;
}

.color_27ae60 {
  fill: #27ae60;
}

.color_2c3e50 {
  fill: #2c3e50;
}

.color_f39c12 {
  fill: #f39c12;
}

.color_e74c3c {
  fill: #e74c3c;
}

.color_9b59b6 {
  fill: #9b59b6;
}

.color_FB6964 {
  fill: #fb6964;
}

.color_342224 {
  fill: #342224;
}

.color_472E32 {
  fill: #472e32;
}

.color_BDBB99 {
  fill: #bdbb99;
}

.color_77B1A9 {
  fill: #77b1a9;
}

.color_73A857 {
  fill: #73a857;
}
</style>